@import url(https://fonts.googleapis.com/css?family=Open+Sans:400,700);
.App{margin-right:0;font-family:"Open Sans",sans-serif;font-size:14px;-webkit-font-smoothing:antialiased !important}body.light-mode{background-color:#fff;color:#000}body.light-mode a{color:#0c66e4}body.dark-mode{background-color:#161a1d;color:#fff}body.dark-mode a{color:#579dff}.react-toggle-track-check,.react-toggle-track-x{display:inline-flex;justify-content:center;align-items:center;margin:0 !important;height:100% !important}
.navbar.sticky{position:-webkit-sticky;position:sticky;top:0;z-index:1}body.light-mode .navbar{background-color:#f7f8f9;color:#000}body.light-mode .navbar a{color:#212529}body.dark-mode .navbar{background-color:#1d2125;color:#fff}body.dark-mode .navbar a{color:#fff}body.dark-mode .navbar-nav svg{color:#fff}body.dark-mode .navbar-nav svg:hover{color:#ffc952}.navbar-brand{font-weight:600;letter-spacing:1px;text-transform:uppercase}.navbar-nav .nav-item{letter-spacing:1px;margin-left:20px;text-align:center;text-transform:uppercase;cursor:pointer}.navbar-nav .nav-item:hover{color:#ffc952 !important}.navbar-nav a{color:#212529 !important;display:inline;font-size:1rem}.navbar-nav a:hover{color:#ffc952 !important}.navbar-nav svg{font-size:2em;margin:0px 10px}
body.light-mode .table{background-color:#fff;color:#000}body.light-mode .table thead>tr th{background-color:#fff}body.light-mode .pattern-count{background-color:#fff;color:#000}body.light-mode .table tr:nth-child(odd){background-color:#f1f2f4}body.light-mode .table tr:nth-child(even){background-color:#fff}body.light-mode .table tbody tr:hover{background-color:#dcdfe4;color:#000}body.dark-mode .table{background-color:#161a1d;color:#fff}body.dark-mode .table thead>tr th{background-color:#161a1d}body.dark-mode .pattern-count{background-color:#161a1d;color:#fff}body.dark-mode .table tr:nth-child(odd){background-color:#22272b}body.dark-mode .table tr:nth-child(even){background-color:#161a1d}body.dark-mode .table tbody tr:hover{background-color:#101214;color:#fff}body.dark-mode .modal-content{background-color:#1d2125;color:#fff}body.dark-mode .modal-content .close{color:#fff}.table .row{justify-content:center}.table thead>tr:first-child{display:none}.table thead>tr th{background:#fff;position:-webkit-sticky;position:sticky;top:50px;text-wrap:nowrap}.table .nav-link{padding:0}.table #difficultyProgress{margin-bottom:25px;font-size:11.5px}.table #difficultyProgress>div:nth-child(n+2){margin-top:12px}.table .progress-pie{height:75px}.table .progress-bar-sm{height:5px}.table .easy{background-color:#5cb85c}.table .medium{background-color:#f0ad4e}.table .hard{background-color:#d9534f}.table .companies,.table .patterns{justify-content:space-evenly;margin:0}.table .companies img{padding:5px}.table .react-toggle{top:7px}.table .reset-button{margin-top:10px;margin-bottom:10px;font-size:.7rem}.table .lastSolvedOn{text-wrap:nowrap}
