.tips{text-align:center !important;display:block;overflow-x:auto;padding:.5em;color:#333;background:#f8f8f8}body.light-mode .tips{background-color:#f7f8f9;color:#333}body.dark-mode .tips{background-color:#1d2125;color:#fff}body.dark-mode .tips pre,body.dark-mode .tips code{background-color:#1d2125;color:#fff}
